<template>
  <section class="jumbotron">
    <h2 class="sub-header">用于快速查询的表单</h2>
    <div class="container">
      <input type="text" placeholder="请输入检索内容" v-model="keyWord" class="form-control">
      <button @click="getCompanies" class="btn-lg btn-primary btn-block">搜索</button>
    </div>
  </section>
</template>

<script>
import axios from 'axios'
// import companies from '../end.json'
export default {
  name: 'sSearch',
  data() {
    return {
      keyWord: '',
      companies:{},
    }
  },
  mounted(){
    this.initData()
    // this.getCompanies()
  },
  methods: {
    getCompanies(){

      this.$bus.$emit('updateData', {isLoading: true, users: [], errMsg: ''})
      let usersData = this.companies.company.filter(x => x.title.includes(this.keyWord))
      this.$bus.$emit('updateData', {isLoading: false, users: usersData, errMsg: ''})
      // return companies
    },
    initData(){
      let that = this
      axios.get(`/end.json`).then(
        response => {
          // this.$bus.$emit('updateData', {isLoading: false, users: response.data, errMsg: ''})
          // console.log(response.data)
          that.companies = Object.assign(that.companies,response.data)
        },
        error => {
          console.error(error)
        }
      )
    }

  },
}
</script>

<style scoped>
.container{
  display: flex;
  flex-direction: row;
  width: 100vw;
}
input{
  width: 70vw;
}
h2{
  text-align: center;
}
</style>